// @ts-nocheck
import instanceInterface from '../../../../services/storage-interface/instance-interface';
import axios from "axios";


const get = (defaultValue = {
  items: null,
  itemsSrc: [],

  isAwait: true,
}) => {
  return instanceInterface.get('cart', defaultValue);
};

const set = (newState) => {
  instanceInterface.set('cart', {
    ...get(),
    ...newState
  });
};

const reload = async (args = {}) => {

  const { onSuccess = () => {} } = args;

  set({ isAwait: true });

  axios
      .post('api/shop/', {
        method: 'shop.cart.status'
      })
      .then(response => {

        const { data = {} } = response;
        const { payload = {} } = data;

        if (typeof payload.payload === 'object') {

          const p = payload.payload;

          const { items = [] } = p;

          set({
            items: items,
            itemsSrc: items,

            isAwait: false
          });

          onSuccess();
        }
      });
};


const removeItem = (id, args = {}) => {

  const { onSuccess = (args = {}) => {} } = args;

  const idx = get().items.findIndex(el => el.id === id);

  if (idx === -1) {
    return;
  }

  const item = get().items[idx];

  const { key = '' } = item;

  const newItemsState = [
      ...get().items.slice(0, idx),
      ...get().items.slice(idx + 1)
  ];

  set({ items: newItemsState });

  axios
      .post('api/shop/', {
        method: 'shop.action.handle',
        ctx: 'web',
        action: 'cart_remove',
        key
      })
      .then(response => {
        onSuccess({ response });
      })
};


const changeItemCount = (key, count, args = {}) => {

  const { onSuccess = () => {} } = args;

  axios
      .post('api/shop/', {
        method: 'shop.action.handle',
        ctx: 'web',
        action: 'cart_change',
        key,
        count
      })
      .then(response => {
        onSuccess(response);
      });
};


const addItem = (id, count, args = {}) => {

  const { onSuccess = () => {} } = args;

  axios
      .post('api/shop/', {
        method: 'shop.action.handle',
        ctx: 'web',
        action: 'cart_add',
        id,
        count
      })
      .then(response => {
        onSuccess(response);
      });
};


const changeCartItemsSeries = (items, args = {}) => {

  const { onSuccess = responses => {} } = args;

  const requestList = items.map(item => {

    const {
      key = null,
      count = null
    } = item;

    if (   key !== null
        && count !== null
    ) {
      return axios.post('api/shop/', {
        method: 'shop.action.handle',
        ctx: 'web',
        action: 'cart_change',
        key,
        count
      });
    }
  });

  set({ isAwait: true });

  axios
      .all(requestList)
      .then(axios.spread((...responses) => {
        set({ isAwait: false });
        onSuccess(responses);
      }));
};


const isAvailable = () => {
  return !get().isAwait;
};


