/* eslint-disable */
// @ts-nocheck
import axios from 'axios';
import dataSetInterface from '../../../../services/storage-interface/data-set-interface';

const getCode = () => 'orderDeliveryCity';

const get = (defaultValue = []) => {
  return dataSetInterface.get(getCode(), defaultValue);
};

const set = async (data) => {
  dataSetInterface.set(getCode(), data);
};

const reload = (regionId) => axios
  .post('api/shop/', {
    method: 'shop.city.find',
    criteria: {
      RegionID: regionId,
      IsActive: true
    },
    limit: 500
  })
  .then(response => {
    const { data = {} } = response;
    const { payload = {} } = data;
    const { result = [] } = payload;

    if (Array.isArray(result)) {
      return set(result);
    }
  })
;

export {
  get,
  set,
  reload
}
