/* eslint-disable */
// @ts-nocheck
import storage from '../../storage-interface';

const get = (key, defaultValue = null) => {
  const { dataSet = {} } = storage.getState();
  return dataSet.hasOwnProperty(key) ? dataSet[key] : defaultValue;
};


const set = (code, data) => {

  const { dataSet } = storage.getState();

  const newDsState = {};
  newDsState[code] = data;

  const newState = {
    ...dataSet,
    ...newDsState
  }

  storage.setStoreField('dataSet', newState);
};


export {
  get,
  set,
};
