import * as React from 'react';
import NumberFormat from 'react-number-format';

const formatCost = (cost, currency = 'руб.') => {
    return (
        <>
        <NumberFormat value={cost}
          displayType={'text'}
          thousandSeparator={' '}
          decimalScale={2}
          fixedDecimalScale={true}
        /> {currency}
        </>
    );
};

export default formatCost;
