/* eslint-disable */
// @ts-nocheck
import React from "react";

import CartItemList from "../cart-item-list";
import controller from '../../../controller';

export default function CartFrame() {

  let animationClass = [];

  if (typeof controller.cartFrameState.getVisible() === 'boolean') {
    animationClass = controller.cartFrameState.getVisible()
        ? ['cart-frame-animation-in']
        : ['cart-frame-animation-out']
    ;
  }

  const classes = [
      ...['cart-frame'],
      ...animationClass
  ];

  const hideFrame = e => {
    e.preventDefault();

    controller.cartFrameState.setVisible(false);
  };

  const buttonsStyle = () => {
    return {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'space-between'
    };
  };

  return (
    <div className={ classes.join(' ') } id="cart__frame">
      <div className="container">
        <div className="row">
          <div className="col-xs-12">
            <div className="cart__content-container">
              <div className="row">
                <div className="col-xs-12">
                  <div className="text-center">
                    <div className="cart__header">В моей корзине</div>
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col-xs-12 col-sm-10 col-sm-offset-1 cart-frame-content">

                  <CartItemList />

                  <div className="horizontal__line horizontal__line-blue cart__total__horizontal__line" />

                  <div className="order__cart__total__cost">
                    сумма: <span className="order__cart__total__cost-value">{ controller.orderInstance.get('cart').cost.cartBase } &#8381;</span>
                  </div>

                  <div style={ buttonsStyle() }>
                    <div className="col-xs-12 pt10">
                      <button className="btn btn-default"
                              onClick={ hideFrame }
                      >Скрыть <i className="fa fa-chevron-right" /></button>
                    </div>

                    <div>
                      <a className="btn btn-blue"
                         href="/order" >Перейти к оформлению</a>
                    </div>
                  </div>

                </div>
              </div>

            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
