import React, { useState } from 'react';
import { Modal, Button } from 'react-bootstrap';

import sendRequest from './service';
import { checkPhone, representPhone } from '../../../services/phone-format';

import controller from '../../../controller';


export default function CallbackModal() {

  const [phone, setPhone] = useState('');

  const hide = () => controller.callbackModalState.setVisible(false);

  const submit = () => {
    sendRequest('8' + phone)
      .then(response => {
        console.log(response);
        const { data = {} } = response;
        const { result = false } = data;
        if (result) {
          hide();
          controller.globalResultModalState.setContent(<p className="text-center">Оператор свяжется с Вами в ближайшее время</p>);
          controller.globalResultModalState.setVisible(true);
        }
      });
  }

  return (
    <>
      <Modal show={ controller.callbackModalState.getVisible() }
             onHide={ e => controller.callbackModalState.setVisible(false) }>
        <Modal.Header closeButton>
          <Modal.Title>Обратный звонок</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="row">
            <div className="col-3">
              <p>Телефон</p>
            </div>

            <div className="col-6">
              <input className="form-control"
                     type="text"
                     value={ representPhone(phone) }
                     onChange={ e => setPhone(checkPhone(`${ e.target.value }`) ) }/>
            </div>
          </div>

        </Modal.Body>
        <Modal.Footer>
          <Button variant={ checkPhone(phone).length < 10 ? 'outline-info disabled' : 'outline-info' }
                  disabled={ checkPhone(phone).length < 10 }
                  onClick={ submit }>
            Жду звонка
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  )
}
