/* eslint-disable */
// @ts-nocheck
import axios from 'axios';


const dictCarModelFamily = {

  _ds: null,

  get() {
    return this._ds;
  },

  reload(args) {

    const { brandId = '', onSuccess = response => {} } = args;

    axios
        .post('api/shop/', {
          method: 'shop.carfiltermodelfamily.list',
          car_brand_id: `${ brandId }`
        })
        .then(response => {
          const { data = {} } = response;
          const { payload = {} } = data;

          if (   payload.hasOwnProperty('payload')
              && Array.isArray(payload.payload)
          ) {
            this._ds = payload.payload;
          }

          onSuccess(response);
        });
  }
};

export default dictCarModelFamily;
