// @ts-nocheck
import React, { Component } from "react";

import CartItemList from '../../app/components/common/cart-item-list';
import { renderOne } from '../../services/safe-render';
import OrderCartTotalCost from './components/order-cart-total-cost';
import ReceiverForm from './components/receiver-form';
import DeliveryForm  from './components/delivery-form';
import PaymentForm from "./components/payment-form";
import SubmitButton from "./components/submit-button";

import controller from '../../app/controller';


export default class OrderPage extends Component {

  componentDidMount() {
    controller.orderInstance.initOrder();
  }


  render() {
    if (   Array.isArray(controller.cartInstance.get().items)
        && controller.cartInstance.get().items.length > 0
    ) {
      renderOne(<CartItemList />, document.getElementById('cart'));
    } else {
      renderOne(<h2>Ваша корзина пуста</h2>, document.getElementById('cart'));
    }

    renderOne(<OrderCartTotalCost />, document.getElementById('order-cart-total-cost'));

    renderOne(<ReceiverForm />, document.getElementById('receiver'));

    renderOne(<DeliveryForm />, document.getElementById('delivery'));

    renderOne(<PaymentForm />, document.getElementById('payment'));

    renderOne(<SubmitButton />, document.getElementById('order-submit-button-container'));

    return null;
  }
}
