// @ts-nocheck
import React from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';

import { renderOne } from "../services/safe-render";
import storage from '../services/storage-interface';

// import OrdersHistory from '../client/orders-history';
import Notifier from './components/notifier';

import { getProperty } from './helper';

import Sidebar from '../sidebar';
import { NavigationSidebarContent } from "../sidebar/components/navigation-sidebar-content";


export const setStoreField = (Field, Value) => {
  storage.setStoreField(Field, Value);
};


export const setStoreFieldSilent = (Field, Value) => {
  storage.setStoreFieldSilent(Field, Value);
};


const onAfterExecAction = (Arguments) => {
  const Action = getProperty(Arguments, 'Action', '');

  switch (Action) {
    case 'handle':
      onAfterHandle(Arguments);
      break;

    case 'get':
      onAfterGet(Arguments);
      break;
  }
};


const onAfterHandle = (Arguments) => {
  const Target = getProperty(Arguments, 'Target', '');

  switch (Target) {
    case 'action':
      onAfterHandleMinishopAction(Arguments);
      break;
  }
};


const onAfterHandleMinishopAction = (Arguments) => {
  let action = getProperty(Arguments, 'action', '');

  let Data = getProperty(Arguments, 'Data', {});

  let Payload = getProperty(Data, 'payload', {});

  let success = getProperty(Payload, 'success', false);

  switch (action) {
    case 'order_add':
      const Field = getProperty(Arguments, 'FieldName', {});

      onAfterChangeOrderFieldValue(Field, getProperty(Data, 'payload', {}));
      break;


    case 'order_getrequired':
      if (success) {
        setStoreField('Required', getProperty(getProperty(Payload, 'data', {}), 'requires', []));
      }
      break;
  }
};


const onAfterGet = (Arguments) => {
  let target = getProperty(Arguments, 'Target', '');

  let data = {};

  switch (target) {

    case 'orderhistory':
      data = getProperty(Arguments, 'Data', {});

      let ordersHistory = [];
      let keys = Object.keys(data);

      for (let i = (keys.length - 1); i >= 0; i--) {
        if (typeof data[keys[i]] === 'object') {
          ordersHistory.push(data[keys[i]]);
        }
      }

      setStoreField('OrdersHistory', ordersHistory);
      break;

    case 'navigation':
      data = getProperty(Arguments, 'Data', []);

      const { payload = [] } = data;

      setStoreField('SidebarMainNav', payload);
      break;
  }
};


const invokeEvent = (Event, Params) => {
  switch (Event) {
    case 'onAfterExecAction':
      onAfterExecAction(Params);
      break;
  }
};


export const cartAdd = (ProductID, Count= 4) => {
  execAction(
    {
      Context: 'shop',
      Target: 'action',
      Action: 'handle',
      ParamValues: {
        ctx: 'web',
        action: 'cart_add',
        id: ProductID,
        count: Count
      }
    }
  );
};


const onAfterChangeOrderFieldValue = (Field, Payload) => {
  const success = getProperty(Payload, 'success', false);

  switch (Field) {
    case 'email':
      if (! success) {
        notify('Ошибка', 'Неверный формат Email адреса', 'error');
      }
      break;
  }
};


const renderNotification = () => {
  ReactDOM.render(
    <Notifier NotifyList={storage.getState().NotifyList}
    />,
    document.getElementById('NotificationContainer')
  );
};


export function notify(Headline, Message, type='success') {
  setStoreField('NotifyList', []);

  setStoreField('NotifyList', [
    {
      Headline: Headline,
      Message: Message,
      type: type
    }
  ]);

  renderNotification();
}


const SidebarMenu = () => {
  return <NavigationSidebarContent />;
};


const NavigationSidebarComponent = () => {
  const getNavigationSidebarHeader = () => {
    return 'Навигация';
  };

  return <Sidebar header={getNavigationSidebarHeader()}
                  isVisible={getProperty(storage.getState(), 'NavigationSidebarVisible', null)}
                  hideHandler={hideNavigationSidebar}
                  content={SidebarMenu()}
  />;
};


export const render = () => {
  renderOne(<OrdersHistoryComponent />, document.getElementById('OrdersHistoryContainer'));
  renderOne(<NavigationSidebarComponent />, document.getElementById('SidebarContainer'));
};


const setNavigationSidebarState = (isVisible) => {
  setStoreField('NavigationSidebarVisible', isVisible);
};


export const showNavigationSidebar = () => {
  setNavigationSidebarState( true);
};


const hideNavigationSidebar = () => {
  setNavigationSidebarState( false);
};


const execAction = (Arguments) => {
  let Options = {
    method: [
      getProperty(Arguments, 'Context', ''),
      getProperty(Arguments, 'Target', ''),
      getProperty(Arguments, 'Action', '')
    ].join('.'),
  };

  Object.assign(Options, getProperty(Arguments, 'ParamValues', {}));

  axios
    .post('/api/shop/', Options)
    .then((Response) => {
      let ResponseResult = {
        Data: getProperty(Response.data,'payload',
          getProperty(Response.data, 'result',
            getProperty(Response.data, 'data', {})
          )
        ),
        Action: getProperty(Arguments, 'Action', ''),
        Target: getProperty(Arguments, 'Target', ''),
        action: getProperty(getProperty(Arguments, 'ParamValues', {}), 'action', '')
      };

      if (ResponseResult.action == 'order_add' ) {
        ResponseResult.FieldName = getProperty(getProperty(Arguments, 'ParamValues', {}), 'key', {});
      }

      invokeEvent('onAfterExecAction', ResponseResult);
    });
};


export const getCartTotalCount = () => {
  const {
    CartTotalCount = 0
  } = storage.getState();

  try {
    return parseInt(CartTotalCount);
  } catch (e) {
    return 0;
  }
};


export const reloadOrdersHistory = () => {
  execAction(
    {
      Context: 'shop',
      Target: 'orderhistory',
      Action: 'get'
    }
  )
};


const reloadSmartOrdersHistory = () => {
  if (typeof document.getElementById('OrdersHistoryContainer') === 'object') {
    reloadOrdersHistory();
  }
};


const OrdersHistoryComponent = () => {
  const getOrdersHistory = () => {
    return storage.getState().OrdersHistory;
  };

  return <OrdersHistory orders={getOrdersHistory()} />;
};


const reloadNavigation = () => {
  execAction(
    {
      Context: 'shop',
      Target: 'navigation',
      Action: 'get'
    }
  )
};


export const init = () => {
  render();
  reloadNavigation();
  reloadSmartOrdersHistory();
};


export const getNavigationContent = () => {
  return getProperty(storage.getState(), 'SidebarMainNav', []);
};


export const getIsLoadedProfile = () => {
  return getProperty(storage.getState(), 'IsLoadedProfile', false);
};

export const setIsLoadedProfile = (isLoadedProfile) => {
  setStoreField('IsLoadedProfile', isLoadedProfile);
};
