class Cost {
  constructor(ds = {}) {

    const {
      cartBase = 0.00,
      deliveryBase = 0.00,
      deliveryResult = 0.00,
      orderBase = 0.00,
      orderResult = 0.00,
      paymentBase = 0.00
    } = ds;

    this.cartBase = cartBase;

    this.deliveryBase = deliveryBase;
    this.deliveryResult = deliveryResult;

    this.orderBase = orderBase;
    this.orderResult = orderResult;

    this.paymentBase = paymentBase;
  }
}

class Receiver {
  constructor(ds = {}) {

    const {
      email = '',
      name = '',
      surname = '',
      patronymic = '',
      phone = '',
      comment = ''
    } = ds;

    this.email = email;
    this.name = name;
    this.surname = surname;
    this.patronymic = patronymic;
    this.phone = phone;
    this.comment = comment;
  }
}

class Delivery {
  constructor(ds = {}) {

    const {
      regionId = '',
      cityId = '',
      city = '',
      deliveryType = '',
      deliveryId = '',
      street = '',
      building = '',
      index = '',
    } = ds;

    this.deliveryId = deliveryId;
    this.deliveryType = deliveryType;
    this.regionId = regionId;
    this.cityId = cityId;
    this.city = city;
    this.index = index;
    this.street = street;
    this.building = building;
  }
}

class Payment {
  constructor(ds = {}) {

    const {paymentId = ''} = ds;

    this.paymentId = paymentId;
  }
}

class OrderState {
  constructor(ds = {}) {
    const {
      isSubmitting = false,
      isUserAgree = true,
      isLoadCosts = false
    } = ds;

    this.isSubmitting = isSubmitting;
    this.isUserAgree = isUserAgree;
    this.isLoadCosts = isLoadCosts;
  }
}

class Order {
  constructor() {
    this.cost = new Cost();
    this.receiver = new Receiver();
    this.delivery = new Delivery();
    this.payment = new Payment();

    this.state = new OrderState();
  }
}


const getCode = () => 'order';

const get = () => {
  return instanceInterface.get(getCode()) ?? (new Order());
};

const set = async (data, args = {}) => {

  const getCurrentState = code => {
    if (code === 'delivery') {
      const {delivery = {}} = get();
      return delivery;
    } else if (code === 'receiver') {
      const {receiver = {}} = get();
      return receiver;
    } else if (code === 'payment') {
      const {payment = {}} = get();
      return payment;
    } else {
      return {};
    }
  };

  const {delivery = {}, payment = {}, receiver = {}} = data;
  const deliveryChanges = updatedDiff(getCurrentState('delivery'), delivery);
  const paymentChanges = updatedDiff(getCurrentState('payment'), payment);
  const receiverChanges = updatedDiff(getCurrentState('receiver'), receiver);
  const changes = {...deliveryChanges, ...paymentChanges, ...receiverChanges};

  instanceInterface.set(getCode(), {
    ...get(),
    ...data
  });

  return changes;

  // return get();
};


const reloadCost = async () => axios
  .post('api/shop/', {
    method: 'shop.allcost.get'
  })
  .then(response => {
    const {data = {}} = response;
    const {payload = {}} = data;

    const p = payload.payload ?? {};

    const {
      cart_base = 0.00,
      delivery_base = 0.00,
      delivery_result = 0.00,
      order_base = 0.00,
      order_result = 0.00,
      payment_base = 0.00,
    } = p;

    const costInstance = new Cost();
    costInstance.cartBase = cart_base;
    costInstance.deliveryBase = delivery_base;
    costInstance.deliveryResult = delivery_result;
    costInstance.orderBase = order_base;
    costInstance.orderResult = order_result;
    costInstance.paymentBase = payment_base;

    return set({cost: costInstance});
  })
;

const reloadAll = async () => reloadCost();


