const checkUserAgree = () => {
  const { isUserAgree = false } = get().state;

  if (! isUserAgree) {
    notify('Предупреждение',
      'Для оформления заказа примите условия обработки персональных данных',
      'warning'
    );

    return false;
  }

  return true;
};


const checkPaymentId = () => {
  const { paymentId = '' } = get().payment;

  if (typeof paymentId !== 'string') {
    notify('Предупреждение',
      'Укажите способ оплаты',
      'warning'
    );
    return false;
  }

  if (paymentId === '') {
    notify('Предупреждение',
      'Укажите способ оплаты',
      'warning'
    );
    return false;
  }

  const _paymentId = parseInt(paymentId);

  if (! (   (typeof _paymentId === 'number')
         && (_paymentId > 0)
        )
  ) {
    notify('Предупреждение',
      'Укажите способ оплаты',
      'warning'
    );
    return false;
  }

  return true;
};


const checkCartItems = () => {
  const result = controller.cartInstance.get().items && controller.cartInstance.get().items.length > 0;

  if (! result) {
    notify('Предупреждение',
      'Ваша корзина пуста',
      'warning'
    );

    return result;
  }

  return result;
};


const checkBeforeSubmit = () => {
  return (   checkPaymentId()
          && checkCartItems()
          && checkUserAgree()
  );
};


