const submit = async () => _setIsSubmitting(true)
  .then(() => {
    const {
      delivery = {},
      receiver = {},
      payment = {}
    } = get();

    const {
      city = '',
      street = '',
      building = '',
      deliveryId = ''
    } = delivery;

    const {
      email = '',
      name = '',
      surname = '',
      patronymic = '',
      phone = '',
      comment = ''
    } = receiver;

    const {paymentId = ''} = payment;

    const toServer = {
      action: 'order/submit',
      method: 'shop.action.handle',
      city: `${city}`,
      index: '',
      street: `${street}`,
      building: `${building}`,
      email: `${email}`,
      receiver: [name, patronymic, surname].join(' ').trim(),
      phone: `${phone}`,
      comment: `${comment}`,
      delivery: `${deliveryId}`,
      payment: `${paymentId}`
    };

    return axios
      .post('/api/shop/', toServer)
      .then(({data}) => {
        if ((data.code === 200)
          && (data.message === 'ok')
        ) {
          if (data.payload.payload.success) {
            if (data.payload.payload.data.redirect) {
              location.replace(data.payload.payload.data.redirect);
            }
          } else {
            const fieldDict = {
              region: 'Регион',
              city: 'Город',
              street: 'Улица',
              building: 'Дом',
              email: 'Email',
              receiver: 'Имя получателя',
              phone: 'Телефон',
              delivery: 'Способ доставки',
              payment: 'Способ оплаты'
            };

            const fields = data.payload.payload.data.map(fieldName => {
              return `- ${fieldDict[fieldName]}`;
            });

            const message = `${data.payload.payload.message}:<br> ${fields.join('<br>')}`;

            notify('Предупреждение', message, 'warning');
          }
        }
      });

  })
  .finally(() => {
    return _setIsSubmitting(false);
  });


const initOrder = async () => axios
  .post('api/shop/', {
    method: 'shop.order.init'
  })
  .then(({ data }) => {
    const { payload = {} } = data;
    const { result = {} } = payload;

    const {
      order = {},
      citySet = [],
      regionSet = [],
      paymentSet = []
    } = result;

    controller.dictOrderDeliveryRegion.set(regionSet);
    controller.dictOrderDeliveryCity.set(citySet);
    controller.dictOrderPayment.set(paymentSet);

    const {
      regionId = '',
      city = '',
      cityId = '',
      street = '',
      building = '',
      email = '',
      phone = '',
      comment = '',
      deliveryId = '',
      paymentId = '',
      deliveryType = '',
      name = '',
      surname = '',
      patronymic = ''
    } = order;

    controller.dictOrderDeliveryCity.reload(regionId);
    controller.dictOrderDelivery.reload(cityId)

    const { receiver = {} } = get();
    set({
      receiver: {
        ...receiver,
        ...{
          email,
          phone,
          comment,
          name,
          surname,
          patronymic
        }
      }
    });

    const { delivery = {} } = get();
    set({
      delivery: {
        ...delivery,
        ...{
          regionId,
          cityId,
          city,
          deliveryId,
          deliveryType,
          street,
          building
        }
      }
    })
    .then(() => {
      _setPaymentId(paymentId);
    });

  })
;

