// @ts-nocheck
import React from 'react';

import Select from '../../../../app/components/data-controls/select';
import Checkbox from '../../../../app/components/data-controls/checkbox';

import controller from '../../../../app/controller';


const PaymentForm = () => {

  const getPaymentList = () => {

    const { deliveryId = '' } = controller.orderInstance.get().delivery;

    let idx = controller.dictOrderDelivery
        .get([])
        .findIndex(item => {
          return item.id == deliveryId;
        });

    const targetItem = idx > -1 ? {...controller.dictOrderDelivery.get([])[idx]} : {};

    const { availablePaymentList = '[]' } = targetItem;
    const paymentIdList = JSON.parse(availablePaymentList);

    return controller.dictOrderPayment.get([])
            .filter(item => {
              const { id = 0 } = item;
              return paymentIdList.includes(id);
            })
            .map(item => {
              const { id = '', name = '' } = item;
              return {value: id, caption: name};
            });
  };


  return (
    <>
      <div className="order__forms__panel__headline">
        <div className="order__forms__panel__headline-number">3</div>
        <div className="order__forms__panel__headline-caption">Как оплатить</div>
      </div>

      <div className="order__forms__payment__form">
        <div className="form__group">
          <Select optionList={ getPaymentList() }
                  value={ controller.orderInstance.get().payment.paymentId }
                  defaultCaption={ 'Выберите способ оплаты' }
                  onChange={ e => controller.orderInstance.setPaymentId(`${ e.target.value }`) }
          />
        </div>
      </div>

      <div className="order__result">
        <div className="order__result__delivery">
          <div className="order__result__delivery__label">
            Доставка
          </div>

          <div className="order__result__delivery__caption">{ controller.orderInstance.get().cost.deliveryBase } &#8381;</div>

        </div>

        <div className="horizontal__line horizontal__line-blue order__total__horizontal__line" />

        <div className="order__result__total">
          <div className="order__result__total__label">
            Сумма с доставкой
          </div>

          <div className="order__result__total__caption">{ `${ controller.orderInstance.get().cost.orderResult }` } &#8381;</div>

        </div>

      </div>

      <div className="order__process__personal__data__checkbox">
        <div className="form__group">
          <Checkbox value={ controller.orderInstance.get().state.isUserAgree }
                    onClick={ () => controller.orderInstance._setIsUserAgree(!controller.orderInstance.get().state.isUserAgree) }
                    label={ 'Даю согласие на обработку персональных данных' }
          />
        </div>
      </div>
    </>
  );
}

export default PaymentForm;
