/* eslint-disable */
// @ts-nocheck
import axios from 'axios';
import dataSetInterface from '../../../../services/storage-interface/data-set-interface';

const getCode = () => 'orderDelivery';

const get = (defaultValue = []) => {
  return dataSetInterface.get(getCode(), defaultValue);
};

const set = (data) => {
  dataSetInterface.set(getCode(), data);
};

const reload = cityId => axios
  .post('api/shop/', {
    method: 'shop.delivery.find',
    cityId
  })
  .then(response => {
    const { data = {} } = response;
    const { payload = {} } = data;

    return set(payload);
  })
;

export {
  get,
  set,
  reload
}
