// @ts-nocheck
import storage from '../../storage-interface';

const get = (key, defaultValue = null) => {
  const { instance = {} } = storage.getState();
  return instance.hasOwnProperty(key) ? instance[key] : defaultValue;
};


const set = (code, data) => {

  const { instance } = storage.getState();

  const newState = {
    ...instance,
    [code]: data
  }

  storage.setStoreField('instance', newState);

  // onChangeDataSet({ code });
};


export {
  get,
  set,
};
