// @ts-nocheck
import React from 'react';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import { RoundIcon } from '../common/components';

export default function Sidebar({ header, isVisible, hideHandler, content }) {
    let sidebarVisible = 'sidebar-hide';
    let sidebarOverlayVisible = 'sidebar-overlay-hide';

    const setSidebarVisible = () => {
        if (isVisible === null) return sidebarVisible = '';

        if (isVisible === false) {
            return sidebarVisible = 'sidebar-hide';
        } else {
            return sidebarVisible = 'sidebar-show';
        }
    }

    const setSidebarOverlayVisible = () => {
        if (isVisible === null) return sidebarOverlayVisible = 'sidebar-overlay-hide';

        if (isVisible === false) {
            return sidebarOverlayVisible = 'sidebar-overlay-hide';
        } else {
            return sidebarOverlayVisible = 'sidebar-overlay-show';
        }
    }

    const handleClose = () => {
        hideHandler();
    };


    let sidebarClassList = [
        'sidebar',
        setSidebarVisible()
    ].join(' ');

    return (
        <>
            <div className={setSidebarOverlayVisible()} id="sidebar-overlay" onClick={handleClose} role="button" />

            <div className={sidebarClassList}>
                <div className="sidebar-header shadow-material-2">
                    <div className="flex-start">
                        <div>
                            <RoundIcon onClick={handleClose} icon={faTimes} />
                        </div>

                        <div>
                            <div className="sidebar-name">
                                {header}
                            </div>
                        </div>

                    </div>
                </div>

                <div className="sidebar-content" data-scrollbar>
                    {content}
                </div>

            </div>
        </>
    )
}