// @ts-nocheck
import React from "react";


const Checkbox = ({
  value = false,
  onClick = e => {},
  label = ''
}) => {

  const addClass = () => value ? ['checked'] : ['not-checked'];

  return <div className={ [...['checkbox__control'], ...addClass()].join(' ') }
              onClick={ onClick }
  >{ label }</div>;
}

export default Checkbox;
