import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';

import { renderOne, renderMany } from '../services/safe-render';

// import { init as initOrderForm,
//          render as renderOrderForm
//        } from '../order/main';
// import { initModalWrapper } from '../common';
// import { Header } from '../header/header';
// import { initProductActions } from '../product-buttons';
// import { initSearchInput } from '../product-search';
// import { initOrdersPage } from '../client';
// import { initProfileData } from '../client/profile';

import Homepage from '../pages/home';
import catalog from '../pages/catalog';
import ProductCard from '../pages/product-card';
import OrderPage from '../pages/order';
import CartFrame from "./components/common/cart-frame";
import PromoList from './components/common/promo-list';

import controller from './controller';
import CallbackModal from "./components/common/callback-modal";
import GlobalResultModal from "./components/common/global-result-modal";


class App extends Component {

  componentDidMount() {
    // initOrderForm();
    // initProductActions();
    // initSearchInput();
    // initProfileData();
    catalog.init();

    controller.cartInstance.reload();
    controller.orderInstance.reloadAll();
    controller.promoSet.reload();
  }

  getCartItemsCount() {
    const items = controller.cartInstance.get({}) || [];
    return items.length;
  }

  render() {

    // renderOrderForm();

    renderMany(<PromoList items={ controller.promoSet.get(null) } />, document.querySelectorAll('.order-promo-container'));

    renderOne(<span onClick={ e => controller.callbackModalState.setVisible(true) }>Заявка на обратный звонок</span>, document.getElementById('header__callback__trigger__placeholder'));

    renderMany(<span>{ this.getCartItemsCount() }</span>, document.querySelectorAll('.cart__count__placeholder'));

    return (
      <>
        {/*<Header />*/}

        <ProductCard />

        {/*<CartFrame />*/}

        <CallbackModal />

        <GlobalResultModal />

        <Router>

          <Route path={'/'} component={ Homepage } exact />

          <Route path={ '/order' } component={ OrderPage } exact />

          {/*<Route path={ '/client/orders' }  render={ initOrdersPage } exact />*/}

        </Router>
      </>
    );
  }
}

const render = () => {
  renderOne(<App />, document.getElementById('frontend__app'));
};

export { render }

