import { onChangeStoreField } from "../../events/onChangeStoreField";


const storage = {

  _state: {
    NotifyList: [],

    OrdersHistory: undefined, // {}

    NavigationSidebarVisible: null,

    SidebarMainNav: [],

    IsLoadedProfile: false,

    dict: {},
    instance: {},
    dataSet: {},
    componentStateInterface: {}
  },

  setHandlers(args = {}) {

    const {
      onAfterUpdateStorageField = (fieldName) => {},
      onAfterUpdateStorage = () => {}
    } = args;

    this.onAfterUpdateStorageField = onAfterUpdateStorageField;
    this.onAfterUpdateStorage = onAfterUpdateStorage;
  },

  setStore(newState) {
    this._state = newState;
    this.onAfterUpdateStorage();
  },

  setState(changes = {}) {
    this.setStore({
      ...this.getState(),
      ...changes
    });
  },

  setStoreFieldSilent(field, value) {

    const changes = {};

    changes[field] = value;

    this.setStore({
      ...this.getState(),
      ...changes
    });

    this.onAfterUpdateStorageField(field);
  },

  setStoreField(fieldName, value) {
    this.setStoreFieldSilent(fieldName, value);

    onChangeStoreField({ fieldName });
  },

  getState() {
    return {...this._state};
  }
};

export default storage;
