import ReactDOM from 'react-dom';

const renderOne = (content, container) => {
  try {
    ReactDOM.render(content, container);
  } catch (e) {
    // console.warn(e);
  }
};


const renderMany = (content, containerList) => {
  if (containerList instanceof NodeList) {
    containerList.forEach(container => {
      renderOne(content, container);
    });
  } else {
    console.warn('Expected containerList typeof NodeList');
  }
};


export { renderOne, renderMany };
