import ReactDOM from "react-dom";

export function getProperty(DataSet, FieldName, DefaultValue) {
    if (   (typeof DataSet[FieldName] == 'undefined')
        || (DataSet[FieldName] === null)
    ) {
        return DefaultValue;
    } else {
        return DataSet[FieldName];
    }
}


export function getListFromDS(Arguments) {
    const DataSet = getProperty(Arguments, 'DataSet', []);

    const Key = getProperty(Arguments, 'KeyProp', '');

    const Prefix = getProperty(Arguments, 'Prefix', '');

    if (Key == '') {
        console.warn('getListFromDS > Key не передан');
        return [];
    }

    let CurrentValue = '';

    return DataSet.map((Element) => {
        CurrentValue = getProperty(Element, Key, '');

        if (CurrentValue != '') {
            return Prefix + CurrentValue;
        }
    });
}


export function getDictFromDS(Arguments) {
    const DataSet = getProperty(Arguments, 'DataSet', []);

    const Key = getProperty(Arguments, 'KeyProp', '');

    if (Key == '') {
        return {};
    }

    const Value = getProperty(Arguments, 'ValueProp', '');

    if (Value == '') {
        return {};
    }

    let Out = {};

    DataSet.map((Element) => {
        Out[getProperty(Element, Key, '')] = getProperty(Element, Value, '');
    });

    return Out;
}


export function makeRequestUrl(BaseURL, ParamDict) {
    let ParamList = [];

    for (let Key in ParamDict) {
        ParamList.push(Key + '=' + encodeURIComponent(ParamDict[Key]));
    }

    return BaseURL + '?' + ParamList.join('&');
}



export function renderOne(Container, Content) {
    if (typeof Container == 'object') {
        ReactDOM.render(Content, Container);
    }
}


export function renderMany(ContainerList, Content) {
    if (!NodeList.prototype.isPrototypeOf(ContainerList)) {
        console.log('Expected NodeList');
        return false;
    }

    ContainerList.forEach((Element) => {
        renderOne(Element, Content);
    });
}
