import React from 'react';


const TextInput = ({
  onBlur = e => {},
  onChange = e => {},
  value = '',
  placeholder = '',
  disabled = false
}) => {

  return <input type="text"
                className="form__control"
                value={ value }
                placeholder={ placeholder }
                disabled={ disabled }
                onBlur={ onBlur }
                onChange={ onChange }
          />;
};

export default TextInput;
