// @ts-nocheck
import React from "react";
import formatCost from "../../../../../../common/format/number";
import ProductCountSwitcher from '../../../../../../pages/catalog/components/product-count-switcher';

import controller from '../../../../../controller';

const CartItem = ({
  item = {}
}) => {

  const wrapItem = (item) => {

    const {
      id = '',
      image = '',
      category = '',
      pagetitle = '',
      article = '',
      price = 0.00,
      count = 0,
      url = '/',
    } = item;

    const totalPrice = price * count;

    const imageStyle = {
      display: 'flex',
      backgroundImage: `url('${ image }')`
    };

    const onRemoveItemClick = e => {
      e.preventDefault();

      controller.cartInstance.removeItem(id, {
        onSuccess: args => {
          controller.cartInstance.reload();
        }
      });
    }

    return (
      <div className="cart__item">

        <div className="cart__item__info">
          <div className="cart__item__info__picture">
            <div className="img img16x17">
              <div className="catalog-grid-product-picture"
                   style={ imageStyle }
              />
            </div>
          </div>

          <div className="cart__item__info__text">
            <div className="cart__item__info__text-category">{ `${ category }` }</div>

            <div className="cart__item__text-name">
              <a className="cart__item__text-name-link" href={ url }><span className="cart__item__text-name-vendor"/>{ `${ pagetitle }` }</a>
            </div>

          </div>

        </div>

        <div className="cart__item__actions">
          <div className="cart__item__actions__content">

            <div className="cart__item__actions__content__vendor-code">{ `${ article }` }</div>

            <div className="cart__item__actions__content__controls">

              <div className="cart__item__actions__content__controls__col">
                <ProductCountSwitcher id={ id } isAwait={ controller.cartInstance.isAvailable() } />
              </div>

              <div className={ ['cart__item__actions__content__controls__col',
                                'cart__item__actions__content__controls__item-price'
                               ].join(' ')
                             }
              >{ `${ totalPrice }` } &#8381;</div>

              <div className="cart__item__actions__content__controls__col">
                <div className="_inline__collection">
                  <div className="_inline__cell _inline__cell__square _inline__cell__extra-small">
                    <div className="_inline__component _inline__component__button" onClick={ onRemoveItemClick } >
                      <img src="https://storage.yandexcloud.net/nik-auto/vezemkolesa/www/assets/template_vlad/common/icons/trash.svg" />
                    </div>
                  </div>
                </div>
              </div>

            </div>

          </div>
        </div>
      </div>
    );
  };

  return wrapItem(item);
};

export default CartItem;
