// @ts-nocheck
import React, { Component } from 'react';
import Select from '../../../../../common/components/base-select';
import controller from '../../../../../app/controller';
import Spinner from '../../../../../app/components/common/spinner';
import dictCarModelFamily from './data/set/car-model-family';
import dictCarModel from './data/set/car-model';


export default class TyreFilterByAuto extends Component {

  state = {
    brandId: '',
    modelFamilyId: '',
    modelId: '',

    brandOptionList: null,
    isLoadingBrandOptionList: false,

    modelFamilyOptionList: null,
    isLoadingModelFamilyOptionList: false,

    modelOptionList: null,
    isLoadingModelOptionList: false
  }


  setBrandOptionList() {

    const ds = controller.dictCarBrand.get(null);

    if (ds === null) {
      return 0;
    }

    this.setState({
      brandOptionList: ds.map(item => {

        const {
          id = '',
          Name = ''
        } = item;

        return {
          id,
          Name
        };
      })
    });
  }


  reloadBrandOptionList() {

    const brandOptionList = controller.dictCarBrand.get(null);

    if (brandOptionList === null) {

      this.setState({ isLoadingBrandOptionList: true });

      controller.dictCarBrand.reload({
        onSuccess: response => {
          this.setState({ isLoadingBrandOptionList: false });
          this.setBrandOptionList();
        }
      })

      return 0;
    }

    this.setBrandOptionList();
  }


  setModelFamilyOptionList() {

    if (dictCarModelFamily.get() === null) {
      return 0;
    }

    this.setState({
      modelFamilyOptionList: dictCarModelFamily.get().map(item => {

        const {
          id = '',
          Name = ''
        } = item;

        return {
          id,
          Name
        };
      })
    });
  }


  reloadModelFamilyOptionList(brandId) {

    this.setState({ isLoadingModelFamilyOptionList: true });

    dictCarModelFamily.reload({
      brandId,
      onSuccess: response => {
        this.setState({ isLoadingModelFamilyOptionList: false });
        this.setModelFamilyOptionList();
      }
    });
  }


  setModelOptionList() {

    const modelOptionList = Array.isArray(dictCarModel.get()) ?
        dictCarModel.get().map(item => {

          const {
            id = '',
            Name = ''
          } = item;

          return {
            id,
            Name
          };
        })
        : null;

    this.setState({ modelOptionList });
  }


  reloadModelOptionList(modelFamilyId) {

    this.setState({ isLoadingModelOptionList: true });

    dictCarModel.reload({
      modelFamilyId,
      onSuccess: response => {
        this.setState({ isLoadingModelOptionList: false });
        this.setModelOptionList();
      }
    });
  }


  componentDidMount() {
    this.reloadBrandOptionList();
  }


  componentDidUpdate(prevProps, prevState) {

    if (this.state.brandId !== prevState.brandId) {
      this.setState({
        modelFamilyId: ''
      });
    }

    if (this.state.modelFamilyId !== prevState.modelFamilyId) {
      this.setState({
        modelId: ''
      });
    }
  }


  render() {

    const onSubmit = e => {

      e.preventDefault();

      const {
        brandId,
        modelFamilyId,
        modelId
      } = this.state;

      if (this.state.modelId === '') {
        return 0;
      }

      const uriConfig = [
        `car_brand=${ brandId }`,
        `car_model_family=${ modelFamilyId }`,
        `car_model=${ modelId }`
      ].join('&');

      window.open('/catalog/shiny/?' + uriConfig);
    };


    const setBrandId = e => {

      e.preventDefault();

      this.setState({
        brandId: e.target.value
      });

      this.reloadModelFamilyOptionList(e.target.value);
    }


    const setModelFamilyId = e => {

      e.preventDefault();

      this.setState({
        modelFamilyId: e.target.value
      });

      this.reloadModelOptionList(e.target.value);
    }


    const getSubmitButtonClasses = (isDisabled) => {
      return isDisabled
        ? 'homepage__top__filter__submit__button homepage__top__filter__submit__button-disabled'
        : 'homepage__top__filter__submit__button'
    };


    return (
      <>
        <form onSubmit={ onSubmit }>
          <div className="homepage__top__filter__control__container">
            <Select OptionList={ this.state.brandOptionList ?? [] }
                    defaultCaption={ 'Марка авто' }
                    disabled={ ! Array.isArray(this.state.brandOptionList) }
                    onChange={ setBrandId }
                    classList={ ['homepage__top__filter__control'] }
            />
          </div>

          <div className="homepage__top__filter__control__container">
            <Select OptionList={ this.state.modelFamilyOptionList ?? [] }
                    defaultCaption={ 'Модель' }
                    disabled={   !Array.isArray(this.state.modelFamilyOptionList)
                              || this.state.isLoadingModelFamilyOptionList
                             }
                    onChange={ setModelFamilyId }
                    classList={ ['homepage__top__filter__control'] }
            />
          </div>

          <div className="homepage__top__filter__control__container">
            <Select OptionList={ this.state.modelOptionList ?? [] }
                    defaultCaption={ 'Не выбрано' }
                    disabled={   !Array.isArray(this.state.modelOptionList)
                              || this.state.isLoadingModelOptionList
                             }
                    onChange={ e => this.setState({ modelId: e.target.value })}
                    classList={ ['homepage__top__filter__control'] }
            />
          </div>

          <div className="homepage__top__filter__control__container">
            <input className={ getSubmitButtonClasses(this.state.modelId.length === 0) }
                   type="submit"
                   disabled={ this.state.modelId.length === 0 }
                   value="Подобрать" />
          </div>
        </form>
      </>
    );
  }
}
