// @ts-nocheck
import React from 'react';

import PromoItem from './components/promo-item';
import EmptyPromoItem from './components/empty-promo-item';


const PromoList = ({
  items = []
}) => {

  const wrapItems = (itemSet = []) => {
    return itemSet.map(item => {

      const {
        offer_id = 0
      } = item;

      return (
        <div key={ `promo_offer_${ offer_id }` }>
          <PromoItem item={ item } />
        </div>
      );
    });
  };

  const renderEmptyItemSet = () => {
    return [0, 1, 2].map(() => {
      return <EmptyPromoItem />;
    });
  };

  if (items === null) {
    return renderEmptyItemSet();
  }

  return wrapItems(items);
};

export default PromoList;
