/* eslint-disable */
// @ts-nocheck
import React from 'react';


const PromoItem = ({
  item = {}
}) => {

  const {
    image = '',
    name = '',
    description_doc = '',
    is_active = false
  } = item;

  const getButtonActiveClass = isActive => isActive ? ['btn-success'] : ['btn-blue'];

  const getButtonActiveCaption = isActive => isActive ? 'активировано' : 'как получить';

  const getButtonClasses = () => {
    return [
        ...['btn', 'btn-block'],
        ...getButtonActiveClass(is_active)
    ]
  };

  const onHandleOpenUrlClick = e => {

    e.preventDefault();

    window.open(description_doc);
  }

  return (
    <div className="white-frame rounded-container-4 shadow-material-1 promo-offer-card-container">
      <div className="promo-offer-card">
        <div className="promo-offer-image-container">
          <div className="img img1x1">
            <div className="flex flex-center">
              <img className="promo-offer-image"
                   src={ image }
                   alt={ name } />
            </div>
          </div>
        </div>

        <div className="promo-offer-content">
          <div className="promo-offer-headline">{ name }</div>

          <div className="promo-offer-button-container">
            <button className={ getButtonClasses().join(' ') }
                    onClick={ onHandleOpenUrlClick }
                    ><span className="text-uppercase"
                           >{ getButtonActiveCaption(is_active) }</span>
            </button>
          </div>

        </div>

      </div>
    </div>
  );
};

export default PromoItem;
