import 'react-app-polyfill/ie11';
import 'react-app-polyfill/stable';
import app from './app';
import storage from './services/storage-interface';

const init = () => {
  storage.setHandlers({
    onAfterUpdateStorageField: () => {},
    onAfterUpdateStorage: () => {
      app.render();
    }
  });

  storage.setStoreFieldSilent('env', 'dev');
};

init();
