// @ts-nocheck
import React, { useState, useEffect } from 'react';

import controller from '../../../../app/controller';


const ProductCountSwitcher = ({
  id = 0
}) => {

  const [needReload, setNeedReload] = useState([]);

  useEffect(() => {

    if (needReload.length > 0) {
      const timeout = setTimeout(() => {

        controller.cartInstance.changeCartItemsSeries(needReload, {
          onSuccess: responses => {
            controller.cartInstance.reload({
              onSuccess: () => {
                setNeedReload([]);
              }
            });

            controller.orderInstance.reloadCost();

            controller.promoSet.reload();
          }
        });
      }, 800);

      return () => {
        clearTimeout(timeout);
      };
    }

  }, [needReload]);

  const getProductList = () => {
    return controller.cartInstance.get().items;
  }

  const getCurrentProductItem = (id) => {
    const productList = getProductList();

    const idx = productList.findIndex(el => el.id === id);

    return productList[idx];
  }

  const getCurrentItemCount = () => {
    const { count = '-' } = getCurrentProductItem(id);
    return count;
  };


  const onLeftButtonClick = e => {

    e.preventDefault();

    const currentItem = getCurrentProductItem(id);

    if (currentItem.count > 1) {
      setItemCount(-1, id);
    }

    setNeedReload(getChangedCountItems());
  };


  const onRightButtonClick = e => {

    e.preventDefault();

    setItemCount(+1, id);

    setNeedReload(getChangedCountItems());
  };

  const getChangedCountItems = () => {

    let itemsChanged = [];

    controller.cartInstance.get().items.forEach(item => {

      const idx = controller.cartInstance.get().itemsSrc.findIndex(el => el.id === item.id);

      if (idx < 0) {
        return;
      }

      const srcState = controller.cartInstance.get().itemsSrc[idx];

      const getCount = (item) => {
        const { count = 0 } = item;

        return count;
      }

      if (getCount(item) !== getCount(srcState)) {
        itemsChanged.push(item);
      }
    });

    return itemsChanged;
  };


  const setItemCount = (step, id) => {
    const idx = getProductList().findIndex(el => el.id === id);
    const currentItem = getCurrentProductItem(id);

    const oldItemState = getProductList()[idx];
    const newItemState = {...oldItemState, count: currentItem.count + step};

    controller.cartInstance.get().items = [
      ...getProductList().slice(0, idx),
      ...[newItemState],
      ...getProductList().slice(idx + 1)
    ];
  }


  const getLeftButtonCaption = () => {
    return (<i className={`fa fa-minus`}/>);
  }


  const getRightButtonCaption = () => {
    return (<i className={`fa fa-plus`}/>);
  }


  return (
    <>
    <div className="_inline__collection">
      <div className="_inline__cell _inline__cell__square _inline__cell__extra-small">
        <button className="_inline__component _inline__component__button _inline__component__button__simple"
                disabled={ !controller.cartInstance.isAvailable() }
                type="button"
                onClick={ onLeftButtonClick }
             ><span className="_inline__component__content">-</span>
        </button>
      </div>

      <div className="_inline__cell _inline__cell__square _inline__cell__extra-small">
        <div className="_inline__component _inline__component__label">
          <span className="_inline__component__content">{ `${ getCurrentItemCount() }` }</span>
        </div>
      </div>

      <div className="_inline__cell _inline__cell__square _inline__cell__extra-small">
        <button className="_inline__component _inline__component__button _inline__component__button__simple"
                disabled={ !controller.cartInstance.isAvailable() }
                onClick={ onRightButtonClick }
          ><span className="_inline__component__content">+</span>
        </button>
      </div>

    </div>


    {/*<div className="input-group">*/}
    {/*  <span className="input-group-btn">*/}
    {/*    <button className="btn btn-default"*/}
    {/*    >{ getLeftButtonCaption() }</button>*/}
    {/*  </span>*/}

    {/*  <input readOnly={ true }*/}
    {/*         type="text"*/}
    {/*         className="form-control text-center"*/}
    {/*         value={ `${ getCurrentItemCount() }` }*/}
    {/*  />*/}

    {/*  <span className="input-group-btn">*/}
    {/*    <button className="btn btn-default"*/}
    {/*            type="button"*/}
    {/*            disabled={ !controller.cartInstance.isAvailable() }*/}
    {/*            onClick={ onRightButtonClick }*/}
    {/*    >{ getRightButtonCaption() }</button>*/}
    {/*  </span>*/}
    {/*</div>*/}
    </>
  );
};

export default ProductCountSwitcher;
