// @ts-nocheck
import storage from '../../../../services/storage-interface';

const callbackModalState = {

  _visible: null,

  getVisible() {
    return this._visible;
  },

  setVisible(visible) {

    if (this._visible !== visible) {
      this._visible = visible;
    }

    storage.setStoreField('callbackModalState', callbackModalState);

    return this;
  }
};

export default callbackModalState;
