import React from 'react';


const Textarea = ({
  onBlur = e => {},
  onChange = e => {},
  value = '',
  placeholder = '',
  disabled = false,
  rows = 4
}) => {

  return <textarea className="form__control"
                   value={ value }
                   placeholder={ placeholder }
                   disabled={ disabled }
                   onBlur={ onBlur }
                   onChange={ onChange }
                   rows={ rows }
  />;
};

export default Textarea;
