// @ts-nocheck
import React from 'react';
import { cartAdd, handleShowMinicartModal } from '../../../../order/main';


const AddToCartButton = ({
    id = 0
}) => {

  const onHandleAddToCartButtonClick = (productId) => {
    try {
      const id = parseInt(productId);
    } catch (e) {
      console.error(e);
      return;
    }

    if (id < 1) {
      return;
    }

    cartAdd(id);
  };

  return (
    <button onClick={ (e) => { onHandleAddToCartButtonClick(id); } }
            className={ 'btn btn-block btn-default' }
            >Добавит в <i className={ 'fa fa-shopping-cart' } /></button>
  );
};

export default AddToCartButton;
