// @ts-nocheck
import storage from '../../../../services/storage-interface';

const globalResultModalState = {

  _visible: false,

  _content: null,

  getVisible() {
    return this._visible;
  },

  setVisible(visible) {

    if (this._visible !== visible) {
      this._visible = visible;
    }

    storage.setStoreField('globalResultModalState', globalResultModalState);

    return this;
  },

  getContent() {
    return this._content;
  },

  setContent(content) {
    this._content = content;

    storage.setStoreField('globalResultModalState', globalResultModalState);

    return this;
  }
};

export default globalResultModalState;
