/* eslint-disable */
// @ts-nocheck
import React, { Component } from "react";

import Select from '../../../../../common/components/base-select';
import axios from "axios";
import Spinner from "../../../../../app/components/common/spinner";


export default class TyreFilterByProps extends Component {

  state = {
    season: '',
    vendor: '',
    width: '',
    height: '',
    diameter: '',
    isLoading: false,
    isAwaitResponse: false,

    seasonOptionList: [
      {
        Name: 'Лето',
        id: 'leto'
      }
      ,{
        Name: 'Зима',
        id: 'zima'
      }
    ],

    vendorOptionList: null,
    widthOptionList: null,
    heightOptionList: null,
    diameterOptionList: null
  }


  reloadAllOptionLists() {

      this.setState({ isLoading: true });

      const apiUrl = '/api/shop/';
      const method = { method: 'shop.filteroptions.get' };

      axios
        .all([
          axios.post(apiUrl, { code: 'filterTyreVendor' , ...method}),
          axios.post(apiUrl, { code: 'filterTyreWidth' , ...method}),
          axios.post(apiUrl, { code: 'filterTyreHeight' , ...method}),
          axios.post(apiUrl, { code: 'filterTyreDiametr' , ...method})
        ])
        .then(axios.spread((...responses) => {

          let stateOptionLists = {
            isLoading: false
          };

          const getPayload = (r) => {
            const { data = {} } = r;
            const { payload = {} } = data;
            return payload;
          };

          const prepareOptionList = ds => {
            return ds.map(item => {

              const {
                Value = ''
              } = item;

              return {
                id: Value,
                Name: Value
              };
            });
          };

          const prepareVendorOptionList = ds => {
            return ds.map(item => {
              const {
                id = '',
                Value = ''
              } = item;

              return {
                id,
                Name: Value
              };
            });
          };

          responses
            .forEach((response, i) => {

            const data = getPayload(response);
            const { payload = [] } = data;

            if (Array.isArray(payload)) {
              if (i === 0) { // vendorOptionList
                stateOptionLists = {...stateOptionLists, ...{ vendorOptionList: prepareVendorOptionList(payload).sort() } };
              } else if (i === 1) { // widthOptionList
                stateOptionLists = {...stateOptionLists, ...{ widthOptionList: prepareOptionList(payload) } };
              } else if (i === 2) { // heightOptionList
                stateOptionLists = {...stateOptionLists, ...{ heightOptionList: prepareOptionList(payload) } };
              } else if (i === 3) { // diameterOptionList
                stateOptionLists = {...stateOptionLists, ...{ diameterOptionList: prepareOptionList(payload) } };
              }
            }
          });

          this.setState(stateOptionLists);
      }));
  }


  componentDidMount() {
    this.reloadAllOptionLists();
  }


  sendRequest() {
    const api = 'assets/components/assortiment/catalog/';

    const params = {
        categoryId: '1',
        options: {
          season: [`${ this.state.season }`],
          width: [`${ this.state.width }`],
          height: [`${ this.state.height }`],
          diametr: [`${ this.state.diameter }`]
        },
        page: '1',
        vendor: `${ this.state.vendor }`
    };

    this.setState({ isAwaitResponse: true });

    axios
      .post(api, params)
      .then((Response) => {
        let path = Response.data.payload.uri;
        window.open(path,"_self");
      })
      .finally(() => {
        this.setState({ isAwaitResponse: false });
      });
  };


  render() {

    const getButtonClassString = () => {

      return [
        'btn',
        'btn-success',
        'col-xs-12',
        'col-sm-6',
        'col-sm-offset-3'
      ].join(' ');
    };


    const onSubmit = e => {
      e.preventDefault();
      this.sendRequest();
    };


    const isSetThoughOneParameter = () => {
      return (   this.state.vendor.length
               + this.state.season.length
               + this.state.width.length
               + this.state.height.length
               + this.state.diameter.length
             ) > 0;
    };


    const getSubmitButtonClass = (isDisabled) => {
      return isDisabled
        ? 'homepage__top__filter__submit__button homepage__top__filter__submit__button-disabled'
        : 'homepage__top__filter__submit__button'
        ;
    };


    return (
      <form onSubmit={ onSubmit } >
        <div className="homepage__top__filter__control__container">
          <Select OptionList={ this.state.seasonOptionList }
                  defaultCaption={ 'Сезон' }
                  onChange={ e => this.setState({ season: e.target.value }) }
                  classList={ ['homepage__top__filter__control'] }
          />
        </div>

        <div className="homepage__top__filter__control__container">
          <Select OptionList={ this.state.vendorOptionList ?? [] }
                  defaultCaption={ 'Производитель' }
                  disabled={ this.state.isLoading }
                  onChange={ e => this.setState({ vendor: e.target.value }) }
                  classList={ ['homepage__top__filter__control'] }
                  />
        </div>

        <div className="homepage__top__filter__control__container">
          <Select OptionList={ this.state.widthOptionList ?? [] }
                  defaultCaption={ 'Ширина' }
                  disabled={ this.state.isLoading }
                  onChange={ e => this.setState({ width: e.target.value }) }
                  classList={ ['homepage__top__filter__control'] }
          />
        </div>

        <div className="homepage__top__filter__control__container">
          <Select OptionList={ this.state.heightOptionList ?? [] }
                  defaultCaption={ 'Высота' }
                  disabled={ this.state.isLoading }
                  onChange={ e => this.setState({ height: e.target.value }) }
                  classList={ ['homepage__top__filter__control'] }
          />
        </div>

        <div className="homepage__top__filter__control__container">
          <Select OptionList={ this.state.diameterOptionList ?? [] }
                  defaultCaption={ 'Диаметр' }
                  disabled={ this.state.isLoading }
                  onChange={ e => this.setState({ diameter: e.target.value }) }
                  classList={ ['homepage__top__filter__control'] }
          />
        </div>

        <div className="homepage__top__filter__control__container">
          <button className={ getSubmitButtonClass(this.state.isLoading
            || !isSetThoughOneParameter()
            || this.state.isAwaitResponse) }
                  type="submit"
                  disabled={    this.state.isLoading
                             || !isSetThoughOneParameter()
                             || this.state.isAwaitResponse
                           }
          >{ this.state.isLoading || this.state.isAwaitResponse ? <Spinner /> : null } Подобрать</button>
        </div>

      </form>
    );
  }
}
