/* eslint-disable */
// @ts-nocheck
import React from 'react';


const Select = ({
  onChange = e => {},
  value = '',
  optionList = [],
  disabled = false,
  defaultValue = '',
  defaultCaption = 'Не выбрано',
  showDefault = true
}) => {

  const getDefaultOption = () => showDefault ? [{value: defaultValue, caption: defaultCaption}] : [];

  const getOptions = () => {
    return [...getDefaultOption(), ...optionList].map((item, i) => {

      const {
        value = '',
        caption = ''
      } = item;

      return <option value={ value }>{ `${ caption }` }</option>;
    });
  };

  return <select value={ value }
                 className="form__control"
                 disabled={ disabled }
                 onChange={ onChange }>{ getOptions() }</select>
  ;
};

export default Select;
