import React from 'react';
import { Modal } from 'react-bootstrap';
import controller from '../../../controller';

export default function GlobalResultModal() {

  const hide = () => {
    controller.globalResultModalState.setVisible(false);
    controller.globalResultModalState.setContent(null);
  };

  return (
    <Modal show={ controller.globalResultModalState.getVisible() }
           onHide={ hide } >
      <Modal.Header closeButton>
        <Modal.Title>Внимание</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        { controller.globalResultModalState.getContent() }
      </Modal.Body>
    </Modal>
  );
}
