/* eslint-disable */
// @ts-nocheck
import React from 'react';


const Select = ({
  defaultCaption = '',
  defaultValue = '',
  OptionList = [],
  onChange = e => {},
  showDefaultOption = true,
  disabled = false,
  classList = []
}) => {

  const getDefaultOption = () => {
    return <option key={ 0 } value={ defaultValue }>{ defaultCaption }</option>;
  }


  const createOptions = () => {

    const defaultOption = [];

    if (showDefaultOption) {
      defaultOption.push(getDefaultOption());
    }

    return [
      ...defaultOption,
      ...OptionList.map((item, i) => {

        const {
          id = '',
          Name = ''
        } = item;

        return (
            <option key={i + 1} value={ id }>{ Name }</option>
        )
      })
    ];
  };


  return (
      <select onChange={ onChange }
              disabled={ disabled }
              className={ classList.join(' ') }
      >{ createOptions() }</select>
  );
};

export default Select;
