/* eslint-disable */
// @ts-nocheck
import React from "react";
import { getProperty } from "../order/helper";

const MenuGenerator = ({args = {}}) => {

  const {
    OptionList = []
  } = args;

  const wrapHeader = (row) => {
    return (
        <div className="nav-group-header">
          {getProperty(row, 'caption', '')}
        </div>
    );
  };

  const wrapBlock = (row) => {
    return (
        <div className="nav-item">
          <div className={`nav-item-container nav-item-level-${getProperty(row, 'level', '')}`}>
            <div className="nav-item-caption">
              {getProperty(row, 'caption', '')}
            </div>
          </div>
        </div>
    );
  };

  const wrapLink = (row) => {
    return (
        <a className="nav-item"
           href={getProperty(row, 'url', '')}
           title={getProperty(row, 'title', '')}>
          <div className={`nav-item-container nav-item-level-${getProperty(row, 'level', '0')}`}>
            <div className="nav-item-caption">
              {getProperty(row, 'caption', '')}
            </div>
          </div>
        </a>
    )
  }

  const contentDict = OptionList.map((row, i) => {
    let type = getProperty(row, 'type', '');

    switch (type) {
      case 'header':
        return wrapHeader(row);

      case 'block':
        return wrapBlock(row)

      case 'link':
        return wrapLink(row);

      case '':
        return wrapLink(row);

      default:
        break;
    }
  });

  return (
    <div className={args.ContainerClass}>
      {contentDict}
    </div>
  );
};

export default MenuGenerator;
