/* eslint-disable */
// @ts-nocheck
import axios from 'axios';
import dataSetInterface from '../../../../services/storage-interface/data-set-interface';

const getCode = () => 'carBrand';

const get = (defaultValue = []) => {
  return dataSetInterface.get(getCode(), defaultValue);
};

const set = (data) => {
  dataSetInterface.set(getCode(), data);
};

const reload = (args = {}) => {

  const { onSuccess = response => {} } = args;

  axios
      .post('api/shop/', {
        method: 'shop.carfilterbrand.list'
      })
      .then(response => {
        const { data = {} } = response;
        const { payload = {} } = data;

        if (   payload.hasOwnProperty('payload')
            && Array.isArray(payload.payload)
        ) {
          set([...payload.payload]);
        }

        onSuccess(response);
      });
};

export {
  get,
  reload
}
