import React from 'react';
import { useState } from 'react';
import { Modal } from 'react-bootstrap';

import { checkPhone, representPhone } from '../../../services/phone-format';
import sendRequest from './service';

import controller from '../../../controller';


export function FastOrderModal() {

  const getProductData = () => {
    const buttonContainer = document.getElementById('ProductButtonsContainer');
    const productDataString = buttonContainer.getAttribute('data-product');

    const {
      id = '',
      hash = '',
      img = '',
      name = '',
      price = null,
      totalCount = 0,
      isAvailable = false
    } = JSON.parse(productDataString);

    return {
      id,
      hash,
      img,
      name,
      price,
      totalCount,
      isAvailable
    };
  };
  const productData = getProductData();

  const [show, setShow] = useState(false);

  const [phone, setPhone] = useState('');
  const [email, setEmail] = useState('');
  const [name, setName] = useState('');
  const [count, setCount] = useState(1);

  const close = () => setShow(false);
  const handleShow = () => setShow(true);

  const incCount = (count, total) => {
    return count >= total ? total : count + 1;
  };

  const decCount = (count) => {
    return count < 1 ? 0 : (count - 1);
  };

  const isOrderAvailable = () => {
    return    phone.length === 10
           && isEmailValid(email)
           && count > 0
    ;
  };

  const onSubmit = e => {
    e.preventDefault();

    const payload = {
      method: 'shop.order.oneclick',
      product: {
        id: `${ productData.id }`,
        count
      },
      client: {
        phone,
        email,
        name
      }
    };

    const resetForm = () => {
      setCount(1);
      setPhone('');
      setEmail('');
      setName('')
    };

    sendRequest(payload)
      .then(response => {

        const getData = (r) => {
          const { data = {} } = r;
          return data;
        }
        const d = getData(response);

        const getPayload = (d) => {
          const { payload = {} } = d;
          return payload;
        };
        const p = getPayload(d);

        const { payload = {} } = p;
        const { success = false,
                message = '',
                data = {}
        } = payload;

        if (success) {
          close();
          resetForm();
          controller.globalResultModalState.setContent(<p className="text-center">Ваш заказ успешно создан</p>);
        } else {
          controller.globalResultModalState.setContent(<p className="text-center">{ message }</p>);
        }

        controller.globalResultModalState.setVisible(true);
      });
  };

  const isEmailValid = (email) => {
    const checker = require('check-email-valid');
    return checker(email);
  };

  return (
    <>
      <button className="product-cart__one-click my-button"
              onClick={ handleShow }>Купить в 1 клик</button>

      <Modal show={ show }
             onHide={ close } >
        <Modal.Header closeButton>
          <Modal.Title>Заказ в 1 клик</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="one__click__product__container">
            <div className="one__click__product__column__first">
              <div className="one__click__product__image__container">
                <img className="one__click__product__image"
                     src={ productData.img }
                />
              </div>

              <div className="one__click__product__name__container">
                <div className="one__click__product__name" >{ productData.name }</div>
                <div className="one__click__product__cost__per__unit">
                  { productData.price } &#8381; за шт.
                </div>
              </div>
            </div>

            <div className="one__click__product__column__last">
              <div className="one__click__product__count__switch__container">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <button className="btn btn-outline-secondary"
                            type="button"
                            onClick={ () => setCount(decCount(count)) }
                    >-</button>
                  </div>

                  <input className="form-control text-center"
                         type="text"
                         value={ count }
                         disabled={ true }
                  />

                  <div className="input-group-append">
                    <button className="btn btn-outline-secondary"
                            type="button"
                            onClick={ () => setCount(incCount(count, productData.totalCount)) }
                    >+</button>
                  </div>

                </div>

                <div className="one__click__product__total__cost">
                  на { productData.price * count } &#8381;
                </div>

              </div>
            </div>

          </div>

          <hr/>

          <div className="row pt15">
            <div className="col-3">
              <p>Телефон*</p>
            </div>

            <div className="col-9">
              <input className={ phone.length === 10 ? 'form-control is-valid' : 'form-control' }
                     type="tel"
                     value={ representPhone(phone) }
                     onChange={ e => setPhone(checkPhone(`${ e.target.value }`)) }/>
            </div>
          </div>

          <div className="row pt15">
            <div className="col-3">
              <p>email*</p>
            </div>

            <div className="col-9">
              <input className={ isEmailValid(email) ? 'form-control is-valid' : 'form-control' }
                     type="email"
                     value={ email }
                     onChange={ e => setEmail(e.target.value) }/>
            </div>
          </div>

          <div className="row pt15">
            <div className="col-3">
              <p>Имя</p>
            </div>

            <div className="col-9">
              <input className="form-control"
                     type="text"
                     value={ name }
                     onChange={ e => setName(e.target.value) }/>
            </div>
          </div>

        </Modal.Body>
        <Modal.Footer>
          <button className={ isOrderAvailable() ? 'btn btn-outline-primary' : 'btn btn-outline-primary disabled' }
                  disabled={ !isOrderAvailable() }
                  onClick={ onSubmit }>Оформить заказ</button>
        </Modal.Footer>
      </Modal>
    </>
  );
}
