/* eslint-disable */
// @ts-nocheck
import axios from 'axios';


const dictCarModel = {

  _ds: null,

  get() {
    return this._ds;
  },

  reload(args) {

    const { modelFamilyId = '', onSuccess = response => {} } = args;

    axios
        .post('api/shop/', {
          method: 'shop.carfiltermodel.list',
          car_modelfamily_id: `${ modelFamilyId }`
        })
        .then(response => {
          const { data = {} } = response;
          const { payload = {} } = data;

          if (   payload.hasOwnProperty('payload')
              && Array.isArray(payload.payload)
          ) {
            this._ds = payload.payload;
          }

          onSuccess(response);
        });
  }
};

export default dictCarModel;
