// @ts-nocheck
import React from 'react';

import TextInput from '../../../../app/components/data-controls/text-input';
import Select from '../../../../app/components/data-controls/select';

import controller from '../../../../app/controller';
import Checkbox from "../../../../app/components/data-controls/checkbox";


const DeliveryForm = () => {

  const getCityList = () => controller.dictOrderDeliveryCity
    .get([])
    .map(item => {
      const { id = '', name = '' } = item;
      return {
        value: id,
        caption: name
      };
    });


  const getStreetInput = () => {
    return <TextInput className="form__control"
                      value={ `${ controller.orderInstance.get().delivery.street }` }
                      placeholder="Адрес"
                      onChange={ e => controller.orderInstance._setStreet( `${ e.target.value }` ) }
                      onBlur={ e => controller.orderInstance.setStreet( `${ e.target.value }` ) }
                      disabled={ controller.orderInstance.get().delivery.deliveryId === '' }
    />;
  };


  // const getBuildingInput = () => {
  //   return <TextInput className="form__control"
  //                     value={ `${ controller.orderInstance.get().delivery.building }` }
  //                     placeholder="Дом, строение, корпус"
  //                     onChange={ e => controller.orderInstance._setBuilding( `${ e.target.value }` ) }
  //                     onBlur={ e => controller.orderInstance.setBuilding( `${ e.target.value }` ) }
  //                     disabled={ controller.orderInstance.get().delivery.deliveryId === '' }
  //   />;
  // };


  const getDeliveryTypeSelect = () => {

    const getDeliveryTypeList = () => {
      return [
        {
          value: 'delivery',
          caption: 'Доставка'
        }
        ,{
          value: 'pickup',
          caption: 'Самовывоз'
        }
        // TODO добавить транспортную компанию
        // ,{
        //   value: 'partnerDelivery',
        //   caption: 'Транспортная компания'
        // }
      ];
    };

    return <Select optionList={ getDeliveryTypeList() }
            value={ `${ controller.orderInstance.get().delivery.deliveryType }` }
            defaultCaption={ 'Способ получения' }
            defaultValue={ '' }
            onChange={ e => controller.orderInstance.setDeliveryType( `${ e.target.value }` ) }
    />;
  };


  const getDeliveries = () => {

    const { deliveryType = '' } = controller.orderInstance.get().delivery;

    const { delivery = {} } = controller.orderInstance.get();
    const { deliveryId = '' } = delivery;

    const getDeliveryCheckbox = (key, value, onClick, label) => {
      return (
        <div key={ key }
             className="form__group" >
          <Checkbox value={ value }
                    onClick={ onClick }
                    label={ label }
          />
        </div>
      );
    };

    const getDeliveryCheckboxList = (deliveryType) => controller.dictOrderDelivery.get([])
          .filter(item => item.deliveryType == deliveryType)
          .map(item => {
            const { id = '', name = '' } = item;

            const onClick = () => deliveryId == id
                ? controller.orderInstance.setDeliveryId('')
                : controller.orderInstance.setDeliveryId(`${ id }`);

            return getDeliveryCheckbox(`delivery_${ id }`, deliveryId == id, onClick, name);
          });

    const deliveries = getDeliveryCheckboxList(deliveryType);

    return deliveries.length > 0 ? deliveries : <p>Недоступно</p>;
  }


  const getRegionList = () => {
    return controller.dictOrderDeliveryRegion.get([]).map(item => {

      const { id = '', name = '' } = item;

      return {
        value: `${ id }`,
        caption: `${ name }`
      };
    });
  }


  const getCurrentDeliveryData = () => {
    const { deliveryId = '' } = controller.orderInstance.get().delivery;

    const idx = controller.dictOrderDelivery
        .get([])
        .findIndex(item => {
          return item.id == deliveryId;
        });

    return idx > -1 ? {...controller.dictOrderDelivery.get([])[idx]} : {};
  };


  const getDeliveryCost = () => {

    const currentDeliveryData = getCurrentDeliveryData();

    const { fixedPrice = 0, distancePrice = 0, capitalDistance = 0 } = currentDeliveryData;

    const cost = fixedPrice + distancePrice * capitalDistance;

    return cost > 0 ? `${ cost } руб.` : 'Бесплатно';
  };


  const getCurrentDeliveryType = () => {
     const currentDeliveryData = getCurrentDeliveryData();

     const { deliveryType = '' } = currentDeliveryData;

     return deliveryType;
  };


  return (
    <>
      <div className="order__forms__panel__headline">
        <div className="order__forms__panel__headline-number">2</div>
        <div className="order__forms__panel__headline-caption">Как получить</div>
      </div>

      <div className="order__forms__delivery__form">
        <div className="order__forms__delivery__form__delivery-controls-group">
          <div className="form__group">
            <Select optionList={ getRegionList() }
                    value={ `${ controller.orderInstance.get().delivery.regionId }` }
                    defaultCaption={ 'Регион получателя' }
                    defaultValue={ '' }
                    onChange={ e => controller.orderInstance.setRegionId( `${ e.target.value }` ) }
            />
          </div>

          <div className="form__group">
            <Select optionList={ getCityList() }
                    value={ `${ controller.orderInstance.get().delivery.cityId }` }
                    defaultCaption={ 'Укажите город' }
                    onChange={ e => controller.orderInstance.setCityId(`${ e.target.value }`) }
            />
          </div>

        </div>

        <div className="order__forms__delivery__form__delivery-type-container">
          <div className="form__group">
            { getDeliveryTypeSelect() }
          </div>

          { controller.orderInstance.get().delivery.deliveryType !== '' && getDeliveries() }

          {    controller.orderInstance.get().delivery.deliveryId !== ''
            &&
            <>
              <div className="order__forms__delivery__form__delivery-type-description">
                <img className="order__forms__delivery__form__delivery-type-description-icon"
                     src="https://storage.yandexcloud.net/nik-auto/vezemkolesa/www/assets/template_vlad/common/components/order-delivery/icon-delivery.svg"
                     alt="Иконка доставка" />

                <div className="order__forms__delivery__form__delivery-type-description-text">
                  Стоимость:&nbsp;&nbsp;<span
                    className="order__forms__delivery__form__delivery-type-description-cost">{ getDeliveryCost() }</span>
                </div>

              </div>

              <div className="order__forms__delivery__form__delivery-type-hint">
                Внимание, во время резких изменений погодных условий время доставки может меняться
              </div>
            </>
          }
        </div>

        <div className="order__forms__delivery__form__delivery-controls-group">
          {    getCurrentDeliveryType() === 'delivery'
            &&
            <>
              <div className="form__group">
              { getStreetInput() }
              </div>

              {/*<div className="form__group">*/}
              {/*{ getBuildingInput() }*/}
              {/*</div>*/}
            </>
          }
        </div>

      </div>
    </>
  );
};

export default DeliveryForm
