/* eslint-disable */
// @ts-nocheck
import React, { useState } from "react";

export default function AddToCartControl({
  initCount = 1,
  maxCount = null,
  handleAddToCart = (args = {}) => {},
  disabled = true,
}) {

  const [count, setCount] = useState(initCount);

  const onDecCount = e => {
    e.preventDefault();

    let newCount = count - 1;

    if (newCount < 1) {
      newCount = 1;
    }

    setCount(newCount);
  };

  const onIncCount = e => {
    e.preventDefault();

    const newCount = count + 1;

    setCount(newCount);
  };

  const execAddToCartAction = e => {
    e.preventDefault();

    handleAddToCart({ count });
  };

  return (
    <>
      <div className="product-cart__counter">
        <button className="product-cart__minus my-button my-button_grey"
                onClick={ onDecCount } >
          <svg width="16" height="4" viewBox="0 0 16 4" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M7.78829e-07 4L9.53674e-07 -2.22545e-07L16 0L16 4L7.78829e-07 4Z" fill="white"/>
          </svg>
        </button>

        <div className="product-cart__count">{ `${ count }` }</div>

        <button className="product-cart__plus my-button my-button_grey"
                disabled={ typeof maxCount === 'number' && count >= maxCount }
                onClick={ onIncCount }
                >
          <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M0 10V6H16V10H0Z" fill="white"/>
            <path d="M10 16H6L6 1.74846e-07L10 0V16Z" fill="white"/>
          </svg>
        </button>

      </div>

      <button className="product-cart__add-to-cart my-button my-button_accent"
              onClick={ execAddToCartAction }
              disabled={ disabled }
              >Добавить в корзину</button>
    </>
  );
}
