import React from 'react';

import Spinner from '../../../../app/components/common/spinner';

import controller from '../../../../app/controller';


const SubmitButton = () => {

  const handleSubmit = e => {
    e.preventDefault();
    controller.orderInstance.submitOrder();
  }

  const getButtonContent = () => {
    return (
      <div className="order__submit__container">
        <div className="order__submit__button">
          <button className="_button _button__block _button__confirm _button__small"
                  onClick={ handleSubmit }
          >сделать заказ</button>
        </div>
      </div>
    );
  };

  const getSpinnerContent = () => {
    return <p className="text-center"><Spinner /> Создаем заказ</p>;
  };

  return controller.orderInstance.get().state.isSubmitting ? getSpinnerContent() : getButtonContent();
};

export default SubmitButton;
