/* eslint-disable */
// @ts-nocheck
// @ts-ignore
import axios from 'axios';
import dataSetInterface from '../../../../services/storage-interface/data-set-interface';


const getCode = () => 'promo';

const get = (defaultValue = []) => {
  return dataSetInterface.get(getCode(), defaultValue);
};

const set = (data) => {
  dataSetInterface.set(getCode(), data);
};

const reload = (args = {}) => {

  const { onSuccess = response => {} } = args;

  axios
      .post('api/shop/', {
        method: 'shop.promooffers.get'
      })
      .then(response => {
        const { data = {} } = response;
        const { payload = {} } = data;

        const p = payload.payload ?? [];

        if (   typeof p === 'object'
            && Array.isArray(p)
        ) {
          set(p);
        }

        onSuccess(response);
      });
};

export {
  get,
  set,
  reload
}
