import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import * as React from 'react';
import { css } from "@emotion/css";

export function RoundIcon({
  backgroundColor = '#fff',
  iconColor = '#1861a0',
  backgroundSize = 32,
  iconSize = '1x',
  onClick = e => {},
  icon
}) {

  const roundIconStyle = (hasHandler, backgroundColor, backgroundSize) => css({
    backgroundColor,
    height: `${backgroundSize}px`,
    width: `${backgroundSize}px`,
    cursor: hasHandler ? 'pointer' : 'auto',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    borderRadius: '50%'
  });

  return (
    <span onClick={onClick} className={roundIconStyle(Boolean(onClick), backgroundColor, backgroundSize)} >
      <FontAwesomeIcon color={iconColor} icon={ icon } size={iconSize} />
    </span>
  );
}

