// @ts-nocheck
import React, { Component } from "react";
import { renderOne } from '../../services/safe-render';

import AddToCartControl from "../../app/components/common/add-to-cart-control";

import controller from '../../app/controller';
import Spinner from "../../app/components/common/spinner";
import {FastOrderModal} from "../../app/components/common/fast-order/fast-order";
// import { FastOrderButton } from '../../../src/fast-order';

export default class ProductCard extends Component {

  addToCartControl() {
    const node = document.getElementById('ProductButtonsContainer');

    if (node) {
      const json = node.getAttribute('data-product') || '{}';

      const data = JSON.parse(json);

      const {
        id = '0',
        totalCount = 0,
        isAvailable = false
      } = data;

      if (! isAvailable) {
        return <div>Не производится</div>;
      }

      if (totalCount === 0) {
        return <div>Товар закончился</div>;
      }

      let productId = 0;

      try {
        productId = parseInt(id);
      } catch (e) {
        productId = 0;
      }

      const execAddToCart = (args = {}) => {
        const { count = 1 } = args;

        controller.cartInstance.addItem(productId, count, {
          onSuccess: () => {
            controller.cartInstance.reload();
          }
        });
      };

      const checkInCart = () => {

        const itemSet = controller.cartInstance.get().items;

        if (itemSet === null) {
          return null;
        }

        const idx = controller.cartInstance.get().items.findIndex(el => {
          return el.id == productId;
        });

        return idx > -1;
      }

      const oneClickOrderButton = () => {
        // return <FastOrderButton product={ {...data, total_count: totalCount } } totalCount={ totalCount } />;
        return <FastOrderModal />;
      };

      if (productId > 0) {
        const AddToCartControlComponent = () => {

          return (
            <>
              <AddToCartControl initCount={ 4 }
                                maxCount={ totalCount }
                                handleAddToCart={ execAddToCart }
                                disabled={ controller.cartInstance.get().isAwait }
                                />

              { oneClickOrderButton() }
            </>
          );
        }

        const buttonShowCart = () => {
          return (
            <>
              <button className="product-cart__one-click my-button no-margin-left"
                      onClick={ e => controller.cartFrameState.setVisible(true) }
                      >в корзине</button>

              <a className="product-cart__add-to-cart my-button my-button_accent"
                 href="/order"
                 title="Оформить заказ"
                 >Оформить заказ</a>

              { oneClickOrderButton() }

            </>
          );
        };

        const isInCart = checkInCart();

        let component = null;

        if (isInCart === null) {
          component = <Spinner />;
        } else {
          component = isInCart
              ? buttonShowCart()
              : AddToCartControlComponent()
          ;
        }

        return component;
      } else {
        return <div>Нет данных</div>;
      }
    }
  }

  render() {

    renderOne(this.addToCartControl(),
        document.getElementById('ProductButtonsContainer'));

    return null;
  }
}
